import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    scrollBehavior () {
        return { x: 0, y: 0 }
    },
    routes: [

        {
    // =============================================================================
    // MAIN LAYOUT ROUTES
    // =============================================================================
            path: '',
            component: () => import('./layouts/main/Main.vue'),
            children: [
        // =============================================================================
        // Theme Routes
        // =============================================================================
              {
                path: '/',
                name: 'home',
                component: () => import('./views/Home.vue')
              },
              {
                path: '/utilisateurs',
                name: 'utilisateurs',
                component: () => import('./views/pages/users/UserList.vue')
              },
              {
                path: '/ressources-humaines',
                name: 'ressources-humaines',
                component: () => import('./views/pages/rh/Rh.vue')
              },
              {
                path: '/ajouter-utilisateur',
                name: 'Ajouter utilisateur',
                component: () => import('./views/pages/users/AddUser.vue')
              },
              {
                path: '/calendrier',
                name: 'Calendrier',
                component: () => import('./views/pages/calendar/Calendar.vue')
              },
              {
                path: '/facture',
                name: 'Facture',
                component: () => import('./views/pages/invoice/Invoice.vue')
              },
              {
                path: '/bon-location',
                name: 'bon-location',
                component: () => import('./views/pages/rental_voucher/Rental.vue')
              },
              {
                path: '/devis',
                name: 'Devis',
                component: () => import('./views/pages/quote/Quote.vue')
              },
              {
                path: '/stock-reel',
                name: 'stock-reel',
                component: () => import('./views/pages/product/Product.vue')
              },
              {
                path: '/profil',
                name: 'profil',
                component: () => import('./views/pages/user-settings/UserSettings.vue')
              },
            ],
        },
    // =============================================================================
    // FULL PAGE LAYOUTS
    // =============================================================================
        {
            path: '',
            component: () => import('@/layouts/full-page/FullPage.vue'),
            children: [
        // =============================================================================
        // PAGES
        // =============================================================================
              {
                path: '/connexion',
                name: 'connexion',
                component: () => import('@/views/pages/authentication/Login.vue')
              },
              {
                path: '/pages/error-404',
                name: 'page-error-404',
                component: () => import('@/views/pages/authentication/Error404.vue')
              },
            ]
        },
        // Redirect to 404 page, if no match found
        {
            path: '*',
            redirect: '/pages/error-404'
        }
    ],
})

router.afterEach(() => {
  // Remove initial loading
  const appLoading = document.getElementById('loading-bg')
    if (appLoading) {
        appLoading.style.display = "none";
    }
})

export default router
